import React, { Component } from "react";
import Auction2Contract from "./contracts/Auction2.json";
import getWeb3 from "./getWeb3";

import "./App.css";

class App extends Component {
  state = { highestBid: 0, web3: null, accounts: null, contract: null, input: "", info: null };

  componentDidMount = async () => {
    try {
      // Get network provider and web3 instance.
      const web3 = await getWeb3();

      // Use web3 to get the user's accounts.
      const accounts = await web3.eth.getAccounts();

      // Get the contract instance.
      const networkId = await web3.eth.net.getId();
      const deployedNetwork = Auction2Contract.networks[networkId];
      const instance = new web3.eth.Contract(
        Auction2Contract.abi,
        deployedNetwork && deployedNetwork.address,
      );

      const response = await instance.methods.highestBid().call();
      
      // Set web3, accounts, and contract to the state, and then proceed with an
      // example of interacting with the contract's methods.
      this.setState({ web3, accounts, contract: instance, highestBid: response});
    } catch (error) {
      // Catch any errors for any of the above operations.
      alert(
        `Failed to load web3, accounts, or contract. Check console for details.`,
      );
      console.error(error);
    }
  };

  bid = async () => {
    const { accounts, contract } = this.state;

    // Stores the highest bid.
    await contract.methods.bid().send({from: accounts[0], value: this.state.web3.utils.toWei(this.state.input) });

    // Get the value from the contract to prove it worked.
    const response = await contract.methods.highestBid().call();


    // Update state with the result.
    this.setState({ highestBid: response });
  };

  withdraw = async () => {
    const { accounts, contract } = this.state;

    await contract.methods.withdraw().send({from: accounts[0]});

//  Update state with the result.
  };

   thisBid = async () => {
    const { accounts, contract } = this.state;

    const response = await contract.methods.userBalances(accounts[0]).call();

    this.setState({ info: response });
   };

   highestBidder = async () => {
    const { accounts, contract } = this.state;

    const response = await contract.methods.highestBidder().call();

    this.setState({ info: response });
   };


   bidderBalance = async () => {
    const { accounts, contract } = this.state;

    const response = await contract.methods.getBidderBalance(accounts[0]).call();

    this.setState({ info: response });
   };


   contractBalance = async () => {
    const { accounts, contract } = this.state;

    const response = await contract.methods.getContractBalance().call();

    this.setState({ info: response });
   };

 
  myChangeHandler = (event) => {
    this.setState({input: event.target.value}, () => {
	console.log(this.state.input)
    });
  }


  render() {
    if (!this.state.web3) {
      return <div>Loading Web3, accounts, and contract...</div>;
    }
    return (
      <div className="App">
        <h1>Auction Time!</h1>
        <div>The highest bid is: {this.state.highestBid}</div>
	<input type="text" onChange={this.myChangeHandler}/>
	<button onClick={this.bid}>Make Bid</button>
	<h2>Witdraw :(</h2>
	<button onClick={this.withdraw}>Withdraw</button>
	
	<h3>Auction Info</h3>
	<div>Info: {this.state.info}</div>

	<button onClick={this.thisBid}>Show current bidder's bid </button>


	<button onClick={this.highestBidder}>Show highest bidder</button>


	<button onClick={this.bidderBalance}>Show bidder's balance</button>


	<button onClick={this.contractBalance}>Show contract's balance</button>

      </div>
	//<input type="text" onChange={(event) => {this.setState({input_2: event.target.value})}}/>
        // <button onClick={this.withdraw}>Withdraw</button>
//      </div>
    );
  }
}

export default App;
